/* Стиль для WebKit-браузеров */
::-webkit-scrollbar {
  width: 0.622vw; /* Ширина полосы прокрутки */
}

::-webkit-scrollbar-track {
  background: #111; /* Цвет фона полосы прокрутки */
}

::-webkit-scrollbar-thumb {
  background-color: #8888889e; /* Цвет бегунка */
  border-radius: 10px; /* Радиус скругления бегунка */
  border: 1px solid #111; /* Отступ вокруг бегунка */
}

/* Стиль для Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #8888889e #111;
}


.mapboxgl-ctrl-attrib {
    position: absolute;
