.toggle-buttons {
    position: absolute;
    bottom: -2vw;
    right: 2vw;
    display: flex;
    background-color: black;
    border-radius: 1.037vw;
    overflow: hidden;
    z-index: 9999; 
  }
  
  .toggle-button {
    display: flex;
    padding: 0.207vw 0.778vw;
    border: none;
    background: transparent;
    color: #ccc;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    border-radius: 0; /* Remove border-radius for default state */
  }
  
  .toggle-button.active {
    background-color: #ccc;
    color: black;
    border-radius: 20px; /* Add border-radius for active state */
  }
  
  .toggle-button:not(.active):hover {
