  /*sidebar*/

  
.sidebar {
  width: 15vw;
  background-color: #111;
  height: calc(100vh - 60px); /* Adjust based on header height */
  color: white;
  cursor: pointer;
}

.sidebar h3 {
  margin-top: 0;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 1vw 0;
  list-style: none;
}

.sidebar li::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #333;
  transition: width 0.3s ease, left 0.3s ease;
}

.sidebar li:hover::before {
  width: 50%;
  right: 50%;
}



  /*sidebar*/

.main-content {
