.custom-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  border-radius: 10px;
  background-color: #111 !important;
  outline: none;
}

.custom-modal button{
  display: flex;
  background-color: #37b252;
  text-align: center;
  align-items: center;
  margin: auto;
  padding: 0.5vw 3vw;
  border: none;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
}

.custom-modal form{
  display: flex;
  flex-direction: column;
}

.custom-modal input{
  color: #fff;
  background-color: transparent;
  margin-bottom: 1.5vw;
}

input[type="text"][name="name"], input[type="text"][name="telegram"] {
