/* LoadingModal.css */
.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5) !important;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    font-size: 1vw;
  }
  
  .loading-container {
    text-align: center;
    color: white;
  }
  
  .dots {
    position: relative;
    margin-bottom: 0.259vw;
    width: 7.461vw;
    height: 1.244vw;
    margin-left: auto;
    margin-right: auto;
  }
  
  .underline {
    position: absolute;
    bottom: -0.259vw;
    width: 100%;
    height: 0.104vw;
    background-color: white !important;
  }
  
  .vertical-line {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 0.104vw;
    background-color: white !important;
    transform: translateX(-50%);
    border-radius: 0.052vw;
  }
  
  .dot {
    width: 0.746vw;
    height: 0.746vw;
    border-radius: 50%;
    position: absolute;
    opacity: 0;
  }
  
  .dot.white {
    background-color: white !important;
    animation: moveLeft 1.5s infinite linear;
  }
  
  .dot.red {
    background-color: red !important;
    animation: moveRight 1.5s infinite linear;
  }
  
  @keyframes moveLeft {
    0% { transform: translateX(0); opacity: 1; }
    45% { transform: translateX(3.523vw); opacity: 1; }
    50% { opacity: 0; }
    100% { transform: translateX(3.523vw); opacity: 0; }
  }
  
  @keyframes moveRight {
    0% { transform: translateX(3.523vw); opacity: 0; }
    50% { opacity: 0; }
    55% { transform: translateX(3.523vw); opacity: 1; }
    100% { transform: translateX(7.047vw); opacity: 1; }
  }
  
  .dot.white:nth-child(1) { animation-delay: 0s; }
  .dot.white:nth-child(2) { animation-delay: 0.3s; }
  .dot.white:nth-child(3) { animation-delay: 0.6s; }
  .dot.white:nth-child(4) { animation-delay: 0.9s; }
  .dot.white:nth-child(5) { animation-delay: 1.2s; }
  .dot.white:nth-child(6) { animation-delay: 1.5s; }
  .dot.red:nth-child(7) { animation-delay: 0.3s; }
  .dot.red:nth-child(8) { animation-delay: 0.6s; }
  .dot.red:nth-child(9) { animation-delay: 0.9s; }
  .dot.red:nth-child(10) { animation-delay: 1.2s; }
  .dot.red:nth-child(11) { animation-delay: 1.5s; }